@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&family=Montserrat:wght@100;200;300;400;700;800&family=Nanum+Gothic:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&family=Montserrat:wght@100;200;300;400;700;800&family=Nanum+Gothic:wght@400;700;800&display=swap);
.row {
    padding: 0 24px;
    display: flex;
    align-items: center;
    height: 85px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.09);
}
.row__title {
    width: 150px;
}
* {
    margin : 0;
    box-sizing: border-box;
    /* background-color: bisque; */
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none !important;
}

ul {
    list-style-type: none;
    padding: 0;
    display: flex;

}
li {
    width: 150px;
}
  
  ::selection {
    /* color: #0D45BB; */
    background: #0D45BB;
  }

/* section {
    margin-bottom: 84px;
} */
.container {
    width: 100%;
    max-width: 736px;
    margin: 0 auto;
    padding: 165px 0;
}
.flex {
    display: flex;
    align-items: center;
}


/*Margins*/
h1 {
    margin-bottom: 15px;
    /* width: 736px; */
}
.selected {
    margin: 0 30px;
    padding: 0 44px;
    height: 98px;
    border-style: none;
    border-radius: 0;
    border-bottom: 3px solid #0D45BB;
}

.mg-big {
    margin-bottom: 84px;
}
.mg-small {
    margin-bottom: 40px;
}
.mg-tiny {
    margin-bottom: 20px;
}
.bt-basic {
    transition-duration: 0.2s;
    background-color: #fff;
    font-family: "Montserrat", "Nanum Gothic", sans-serif;
    border: 3px solid #000;
    height: 82px;
    padding: 0 20px;
    font-size: 24px;
    font-weight: bold;
}

.bt-blue {
    background-color: #fff;
    color : #0D45BB;
    font-family: "Montserrat", "Nanum Gothic", sans-serif;
    border: 3px solid #0D45BB;
    height: 82px;
    padding: 0 20px;
    font-size: 24px;
    font-weight: bold;
}

.bt-blue:hover {
    transition: background-color 0.5s, color 0.5s linear;

    cursor: pointer;

    background-color: #0D45BB;
    color: #fff;
    border: 3px solid #0D45BB;
}
body {
    font-family: "Montserrat", "Nanum Gothic", sans-serif;
    letter-spacing: -0.015em;
  }

  .ff-karla {
    font-family: "Karla", sans-serif;
    letter-spacing: -0.015em;
  }
  
  /* Font Size */
  
  .fs-tiny {
    font-size: 12px;
    line-height: 1.3333333333;
  }
  
  .fs-small {
    font-size: 14px;
    line-height: 1.4285714286;
  }
  
  .fs-base {
    font-size: 16px;
    line-height: 1.5;
  }
  
  .fs-medium {
    font-size: 18px;
    line-height: 1.5555555556;
  }
  
  .fs-large {
    font-size: 20px;
    line-height: 1.6;
  }
  
  .fs-h2 {
    font-size: 35px;
    line-height: 2.7714285714;
  }

  .fs-h3 {
    font-size: 24px;
    line-height: 2.3333333333;
  }
  
  .fs-h1 {
    font-size: 50px;
    line-height: 1.62;
  }
  
  /* Font Weight */
  
  .fw-light,
  .fw-300 {
    font-weight: 300;
  }
  
  .fw-regular,
  .fw-400 {
    font-weight: 400;
  }
  
  .fw-medium,
  .fw-500 {
    font-weight: 500;
  }
  
  .fw-bold,
  .fw-700 {
    font-weight: 700;
  }
  
  /* Color */
  
  .text-dark {
    color: #1f2d3d;
  }
  
  .text-primary {
    color: #0D45BB;
  }
  
  .text-secondary {
    color: #F3641D;
  }
  
  .text-tertiary {
    color: #c0ccda;
  }
  .text-grey {
    color: rgba(0,0,0,0.37);
  }
  
  .text-white {
    color: #fff;
  }
  
  .text-success {
    color: #13ce66;
  }
  
  .text-error {
    color: #ff5216;
  }
  
  .text-info {
    color: #009eeb;
  }
